import { useState } from "react";
import { format } from "date-fns";
import { Settings } from "lucide-react";
import { Link } from "react-router-dom";
import { useAuth } from "@/hooks/useAuth";
import { useSalesData } from "@/hooks/useSalesData";
import { LOCATIONS } from "@/types/entities";
import type { TimeframeType } from "@/utils/dateUtils";

import StatsGrid from "@/components/Dashboard/StatsGrid";
import LocationSelector from "@/components/Dashboard/LocationSelector";
import LazyChart from "@/components/Dashboard/LazyChart";
import RecentActivity from "@/components/Dashboard/RecentActivity";
import PerformanceMetrics from "@/components/Dashboard/PerformanceMetrics";

export default function Dashboard() {
  const [selectedLocation, setSelectedLocation] = useState("all");
  const [timeframe, setTimeframe] = useState<TimeframeType>("7days");

  const { user, loading: authLoading } = useAuth();
  const {
    salesData,
    stats,
    loading: dataLoading,
  } = useSalesData({
    selectedLocation,
    userLocation: user?.location,
    isAdmin: user?.is_admin,
    timeframe,
  });

  const loading = authLoading || dataLoading;

  if (loading) {
    return (
      <div className="min-h-screen bg-[#0e0e0e] flex items-center justify-center">
        <div className="w-8 h-8 border-2 border-[#bc9a64] border-t-transparent rounded-full animate-spin"></div>
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-[#0e0e0e] text-white p-6">
      <div className="max-w-7xl mx-auto space-y-8">
        {/* Header */}
        <div className="flex flex-col lg:flex-row justify-between items-start lg:items-center gap-6">
          <div>
            <h1 className="text-3xl font-bold mb-2">
              {user?.is_admin
                ? "Analytics Overview"
                : `${user?.location || "Location"} Dashboard`}
            </h1>
            <p className="text-gray-400">
              {format(new Date(), "EEEE, MMMM d, yyyy")}
            </p>
          </div>

          <div className="flex items-center gap-4">
            {user?.is_admin && (
              <LocationSelector
                selectedLocation={selectedLocation}
                onLocationChange={setSelectedLocation}
                locations={LOCATIONS}
              />
            )}
            {user?.is_admin && (
              <Link
                to="/settings"
                className="p-2 rounded-full hover:bg-[#2a2a2a] transition-colors duration-200"
                title="Open Settings"
              >
                <Settings className="h-6 w-6 text-gray-400 hover:text-[#bc9a64]" />
              </Link>
            )}
          </div>
        </div>

        {/* Stats Grid */}
        <StatsGrid stats={stats} />

        {/* Charts and Metrics */}
        <div className="grid lg:grid-cols-3 gap-8">
          <div className="lg:col-span-2">
            <LazyChart
              salesData={salesData}
              timeframe={timeframe}
              onTimeframeChange={setTimeframe}
            />
          </div>

          <div className="space-y-6">
            <PerformanceMetrics
              salesData={salesData}
              selectedLocation={selectedLocation}
            />
          </div>
        </div>

        {/* Recent Activity */}
        <RecentActivity
          salesData={salesData.slice(0, 10)}
          showLocation={user?.is_admin || selectedLocation === "all"}
        />
      </div>
    </div>
  );
}
